import React from 'react';
import { useState } from 'react'
import axios from 'axios'


function FormFiles() {
  var [list, setList] = useState([]);

  const fileChange = (e) => {
    var list = []
    var key = 0;
    for (var i of e.target.files) {
      list.push(<div key={key++}>{i.name}</div>)      
    }
    setList(list);
  }

  const submit = (e) => {
    e.preventDefault();
    const formdata = new FormData(e.target);
    axios({
      method: 'post', 
      url: 'http://localhost:4000/addActivity',
      data: formdata
    })
      .then(data => {
        alert(data)
      })
  }
  return (
    <div className="container">
      <form onSubmit={(e) => { submit(e) }} action="/" method="post" encType="multipart/form-data">
        <label htmlFor="idName">Nombre de la actividad:</label>
        <input id="idName" type="input" name="actName" placeholder="Actividad"  className="form-control"></input><br />
        <label htmlFor="idResponsable">Responsable:</label>
        <input id="idResponsable" type="text" name="responsable" placeholder="Responsable"  className="form-control"></input><br />
        <label htmlFor="idDesciption">Descipción de la actividad:</label>
        <textarea id="idDesciption" name="description" rows="4" cols="50" className="form-control" >
          
        </textarea><br />
        <label htmlFor="idDate"> Fecha en que se realizó la actividad:</label>
        <input id="idDate" type="date" name="date" className="form-control" /><br />
        <label htmlFor="idEvidencia">Evicencias:</label>
        <input id="idEvidencia" type="file" name="myFile" className="form-control" onChange={(e) => fileChange(e)} multiple /><br />
        <ul>
          {list}
        </ul>
        <input className="btn btn-warning" type="submit" value="Enviar"></input>
      </form>
    </div>
  );
}

export default FormFiles;