import react from "react";
import axios from 'axios';

class Card extends react.Component {
    constructor(props) {
        super(props)
        this.state = {
            id:this.props.id
        }

        //this.handleDownload = this.handleDownload.bind(this);
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        //alert(this.state.id)
        axios(
            {
                method: 'get',
                url: 'http://localhost:4000/deleteActivity',
                params: { _id: this.state.id }
            }
        )
            .then(data => {
                alert(data)
            })

    }
     handleDownload = (fl)=>{
        //alert(this.props.folder+"/"+e.target.href);
        //return ;
        axios(
            {
                method: 'get',
                url: 'http://localhost:4000/download',
                //params:  {filename:this.props.folder + "/"+ fl}
                params:  {filename: this.props.folder+"/"+ fl}
            }
        )
            .then(data => {
                alert(data)
            })

    }
    render() {
        const files = [];
        if (this.props.files) {
            this.props.files.map(fl => {
                files.push(<a href="#"   onClick={()=>this.handleDownload(fl)} value={fl} className="card-link">{fl}</a>);
            })
        }

        return (
            <div  className="card w-70 p-3  shadow-lg by-white rounded-start" Key={this.state.id}>
                <h5 className="card-header bg-transparent">{this.props.actName}</h5>
                <p className="fs-6 text-secondary">{this.props.date}. 
                </p>
                <div className="card-body" >
                    <p className="card-text">{this.props.description}</p>
                </div>

                <div className="card-footer" style={{ "backGroundColor": "#eee" }}>
                    <div  >
                        {files}
                    </div>
                    <div  >
                        <button type="button" className="btn btn-danger" onClick={this.handleClick}><i className="bi bi-trash"></i></button>
                    </div>
                </div>


            </div>
        )
    }
}

export default Card